 markdown: "<hr>\n<h2>order: 1\ntitle:\nzh-CN: 基本\nen-US: Alignment\nsubtitle:\nzh-CN: 根据具体目标和制约因素，选择最佳的标签对齐方式。\nen-US: Depending on your design, there are several different ways to align your label element.</h2>\n<pre><code class=\"language-js\">import { Form, Input, Button, Radio } from 'dashkit-ui';\n\nclass App extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.state = {\n      align: 'right',\n    };\n  }\n\n  render() {\n    const { align } = this.state;\n    return (\n      &lt;div&gt;\n        &lt;Radio.Group onChange={this.handleChange} value={align} style={{ marginBottom: 24 }}&gt;\n          &lt;Radio value=&quot;left&quot;&gt;Left&lt;/Radio&gt;\n          &lt;Radio value=&quot;right&quot;&gt;Right&lt;/Radio&gt;\n          &lt;Radio value=&quot;top&quot;&gt;Top&lt;/Radio&gt;\n        &lt;/Radio.Group&gt;\n        &lt;Form labelAlign={align}&gt;\n          &lt;Form.Item label=&quot;Name&quot; required&gt;\n            &lt;Input placeholder=&quot;Please input your name&quot; /&gt;\n          &lt;/Form.Item&gt;\n          &lt;Form.Item label=&quot;Email&quot;&gt;\n            &lt;Input placeholder=&quot;Please input your email&quot; /&gt;\n          &lt;/Form.Item&gt;\n        &lt;/Form&gt;\n      &lt;/div&gt;\n    );\n  }\n\n  handleChange = align =&gt; {\n    console.log(`switch to alignment ${align}`);\n    this.setState({ align });\n  };\n}\n\nReactDOM.render(&lt;App /&gt;, mountNode);\n</code></pre>\n",
